<template>
  <div class="container rooms position-relative">
    <div class="room-filter position-sticky top-0">
      <div class="filter-body d-flex flex-wrap">
        <div class="filter-item d-flex align-items-center justify-content-between flex-grow-1 flex-shrink-1"
          :class="{ active: conditions.type !== undefined }">
          <span @click="modalStatus.isOpenRoomType = true">{{ conditions.type === undefined ? 'Chọn loại phòng' :
              conditions.type
          }}</span>
          <i class="fa-solid fa-delete-left" @click="conditions.type = undefined"></i>
        </div>
        <div class="filter-item d-flex align-items-center justify-content-between flex-grow-1 flex-shrink-1"
          :class="{ active: conditions.location !== undefined }">
          <span @click="modalStatus.isOpenRoomLocation = true">{{ location }}</span>
          <i class="fa-solid fa-delete-left"
            @click="currentDistrict = undefined; conditions.location = undefined; level = 0"></i>
        </div>
        <div class="filter-item d-flex align-items-center justify-content-between flex-grow-1 flex-shrink-1"
          :class="{ active: hasConditionArea }">
          <span @click="modalStatus.isOpenRoomArea = true">{{
              hasConditionArea
                ? `${conditions.area[0]} m2 - ${conditions.area[1]} m2`
                : "Tìm theo diện tích"
          }}</span>
          <i class="fa-solid fa-delete-left" @click="conditions.area[0] = 0; conditions.area[1] = 50"></i>
        </div>
        <div class="filter-item d-flex align-items-center justify-content-between flex-grow-1 flex-shrink-1"
          :class="{ active: hasConditionPrice }">
          <span @click="modalStatus.isOpenRoomPrice = true">{{
              hasConditionPrice
                ? `${toVnd(conditions.price[0])} - ${toVnd(conditions.price[1])}`
                : "Tìm theo giá"
          }}</span>
          <i class="fa-solid fa-delete-left" @click="conditions.price[0] = 0; conditions.price[1] = 10000000"></i>
        </div>
        <div class="filter-item d-flex align-items-center justify-content-between flex-grow-1 flex-shrink-1"
          :class="{ 'active': conditions.orderBy !== undefined }">
          <span @click="modalStatus.isOpenOrderBy = true">{{
              conditions.orderBy !== undefined
                ? orderByOptions[conditions.orderBy]
                : "Sắp xếp theo"
          }}</span>
          <i class="fa-solid fa-delete-left" @click="conditions.orderBy = undefined"></i>
        </div>
        <div class="filter-button flex-grow-1 flex-shrink-1">
          <button type="button" class="btn btn-warning fw-bolder" @click="searchRoom()">Tìm Kiếm</button>
        </div>
      </div>
      <Modal :open="modalStatus.isOpenRoomType" :className="'modal--room-type'" :emitFuncName="'closeRoomType'"
        :headerLabel="'Chọn Loại Phòng'" @closeRoomType="modalStatus.isOpenRoomType = false">
        <ul class="room-type-list">
          <li class="room-type-item" v-for="roomType in roomTypes" :key="roomType"
            :class="{ 'selected': conditions.type === roomType }" @click="conditions.type = roomType">{{ roomType }}
          </li>
        </ul>
      </Modal>
      <Modal :open="modalStatus.isOpenRoomLocation" :className="'modal--room-location'"
        :emitFuncName="'closeRoomLocation'" :headerLabel="'Chọn Vị Trí'" @closeRoomLocation="back()">
        <ul class="location-list">
          <li class="location-item" v-if="level === 0" :class="{ 'selected': currentDistrict === undefined }"
            @click="updateLocationModel(undefined)">Thành phố
            Hà Nội</li>
          <li class="location-item" v-else :class="{ 'selected': currentDistrict.name === conditions.location.name }"
            @click="updateLocationModel(null)">{{
                currentDistrict.name
            }}</li>
          <li class="location-item" v-for="location in locations" :key="location.code"
            :class="{ 'selected': conditions.location !== undefined && conditions.location.name === location.name }"
            @click="updateLocationModel(location)">
            {{
                location.name
            }}</li>
        </ul>
      </Modal>
      <Modal :open="modalStatus.isOpenRoomArea" :className="'modal--room-area'" :emitFuncName="'closeRoomArea'"
        :headerLabel="'Chọn Diện Tích'" @closeRoomArea="modalStatus.isOpenRoomArea = false">
        <Slider class="slider-blue" v-model="conditions.area" :format="{ suffix: ' m2' }" :min="0" :max="50"></Slider>
        <div class="modal-bottom d-grid gap-1">
          <button type="button" class="btn btn-primary" @click="modalStatus.isOpenRoomArea = false">Áp Dụng</button>
        </div>
      </Modal>
      <Modal :open="modalStatus.isOpenRoomPrice" :className="'modal--room-price'" :emitFuncName="'closeRoomPrice'"
        :headerLabel="'Chọn Giá'" @closeRoomPrice="modalStatus.isOpenRoomPrice = false">
        <Slider class="slider-blue" v-model="conditions.price" :step="500000" :format="{ suffix: ' ₫', thousand: '.' }"
          :min="0" :max="10000000">
        </Slider>
        <div class="modal-bottom d-grid gap-1">
          <button type="button" class="btn btn-primary"
            @click="modalStatus.isOpenRoomPrice = !modalStatus.isOpenRoomPrice">Áp Dụng</button>
        </div>
      </Modal>
      <Modal :open="modalStatus.isOpenOrderBy" :className="'modal--order-by'" :emitFuncName="'closeOrderBy'"
        :headerLabel="'Sắp Xếp Theo'" @closeOrderBy="modalStatus.isOpenOrderBy = false">
        <ul class="order-by-list">
          <li class="order-by-item" v-for="(option, index) in orderByOptions" :key="option"
            :class="{ 'selected': conditions.orderBy === index }"
            @click="conditions.orderBy = index; modalStatus.isOpenOrderBy = false">{{ option }}
          </li>
        </ul>
      </Modal>
    </div>
    <div class="room-number-of-search-result" v-if="isSearched">Có {{ rooms.length }} phòng phù hợp</div>
    <ul class="room-list">
      <li class="container room-item" v-for="room in rooms" :key="room.id">
        <Room :room="room"></Room>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";
import Modal from "./Modal.vue";
import Room from "./Room.vue";
import Slider from '@vueform/slider'
import { reactive } from "vue";

export default {
  name: "RoomList",
  components: { Room, Modal, Slider },
  async created() {
    this.$store.commit('setLoading', true)
    const endpoints = [
      "https://api.mysupership.vn/v1/partner/areas/district?province=01",
      "/rooms",
    ];
    const [respLocation, respRooms] = await axios.all(endpoints.map(endpoint => axios.get(endpoint)))
    this.locations = respLocation.data.results
    this.rooms = respRooms.data
    this.$store.commit('setLoading', false)
  },
  data() {
    return {
      modalStatus: reactive({
        isOpenRoomType: false,
        isOpenRoomLocation: false,
        isOpenRoomArea: false,
        isOpenRoomPrice: false,
        isOpenOrderBy: false
      }),
      isSearched: false,
      level: 0,
      currentDistrict: undefined,
      rooms: [],
      locations: [],
      roomTypes: ["Phòng trọ", "CCMN"],
      orderByOptions: ["Giá tăng dần", "Giá giảm dần", "Gần đây nhất"],
      conditions: reactive({
        type: undefined,
        location: undefined,
        area: [0, 50],
        price: [0, 10000000],
        orderBy: undefined
      }),
    };
  },
  computed: {
    hasConditionArea() {
      return this.conditions.area[0] != 0 || this.conditions.area[1] != 50;
    },

    hasConditionPrice() {
      return this.conditions.price[0] != 0 || this.conditions.price[1] != 10000000;
    },

    location() {
      if (this.currentDistrict === undefined) {
        return 'Thành phố Hà Nội'
      }
      if (this.conditions.location.district !== undefined) {
        return `${this.conditions.location.name}, ${this.currentDistrict.name}`
      }
      return this.currentDistrict.name
    }
  },
  methods: {
    async searchRoom() {
      this.$store.commit('setLoading', true)
      const arrCondition = []
      if (this.conditions.type !== undefined) arrCondition.push(`type=${this.conditions.type}`)
      if (this.conditions.location !== undefined) {
        const locationName = this.conditions.location.name
        arrCondition.push(
          this.conditions.location.district === undefined ? `district=${locationName}` : `ward=${locationName}`
        )
      }
      if (this.conditions.area[0] !== 0) arrCondition.push(`area_from=${this.conditions.area[0]}`)

      if (this.conditions.area[1] !== 50) arrCondition.push(`area_to=${this.conditions.area[0]}`)

      if (this.conditions.price[0] !== 0) arrCondition.push(`price_from=${this.conditions.price[0]}`)

      if (this.conditions.price[1] !== 10000000) arrCondition.push(`price_to=${this.conditions.price[1]}`)

      switch (this.conditions.orderBy) {
        case 0: arrCondition.push('order_by=price_asc')
          break
        case 1: arrCondition.push('order_by=price_desc')
          break
        case 2: arrCondition.push('order_by=time_newest')
          break
      }

      if (arrCondition.length === 0) return

      const resp = await axios.get(`/rooms?${arrCondition.join('&')}`)
      this.rooms = resp.data
      this.isSearched = true
      this.$store.commit('setLoading', false)
    },

    toVnd(value) {
      return new Intl.NumberFormat('vi-VN', { style: 'currency', currency: 'VND' }).format(value)
    },

    async updateLocationModel(location) {
      if (location === undefined) {
        this.conditions.location = this.currentDistrict = undefined
        this.modalStatus.isOpenRoomLocation = false
        return
      }
      if (location === null) {
        this.back()
        return
      }
      this.conditions.location = location
      if (location.district === undefined) {
        this.currentDistrict = location
        const resp = await axios.get(`https://api.mysupership.vn/v1/partner/areas/commune?district=${location.code}`);
        this.level = 1
        this.locations = resp.data.results;
      }
      else {
        this.modalStatus.isOpenRoomLocation = false
      }
    },

    async back() {
      if (this.currentDistrict === undefined || this.currentDistrict.name === this.conditions.location.name) {
        this.modalStatus.isOpenRoomLocation = false
      }
      else {
        const resp = await axios.get("https://api.mysupership.vn/v1/partner/areas/district?province=01")
        this.level = 0
        this.locations = resp.data.results
        this.conditions.location = this.currentDistrict;
      }
    }
  },
};
</script>

<style scoped>
.rooms {
  padding-top: 20px;
}

.room-filter {
  z-index: 1;
}

.filter-body {
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  background: #2068cd;
  padding: 10px;
  border-radius: 5px;
}

.filter-item {
  cursor: pointer;
  color: #ddd;
  background: #fff;
  margin: 0px 10px;
  padding: 0px 10px;
  border: none;
  border-radius: 5px;
  font-size: 16px;
}

.filter-item.active {
  color: #000;
  font-weight: 600;
}

.filter-button button {
  width: 100%;
}

.room-type-list,
.location-list,
.order-by-list,
.room-list {
  width: 100%;
  list-style-type: none;
  padding: 0;
  text-align: start;
}

.room-type-item,
.location-item,
.order-by-item {
  padding: 12px 10px 12px 25px;
  border-bottom: 1px solid #ddd;
  font-size: 1.1rem;
  position: relative;
  cursor: pointer;
  transition: all 0.3s ease;
}

.room-type-item.selected,
.location-item.selected,
.order-by-item.selected {
  font-weight: 600;
  color: #007aff;
}

.room-type-item:hover,
.location-item:hover,
.order-by-item:hover {
  color: #007aff;
}

.room-type-item.selected::before,
.location-item.selected::before,
.order-by-item.selected::before {
  background: url('https://phongtro123.com/images/cirlcle-rec-active.svg') center no-repeat;
  background-size: contain;
  opacity: 1;
}

.room-type-item::before,
.location-item::before,
.order-by-item::before {
  background: url('https://phongtro123.com/images/cirlcle-rec.svg') center no-repeat;
  background-size: contain;
  width: 15px;
  height: 15px;
  position: absolute;
  top: 50%;
  left: 0;
  margin-top: -8px;
  display: block;
  content: "";
  opacity: .45;
}

.slider-blue {
  margin-top: 50px;
  width: 80%;
}

.modal-bottom {
  margin-top: 20px;
  width: 100%;
}

.room-number-of-search-result {
  margin-top: 10px;
  font-size: 22px;
  font-weight: 600;
}
</style>>
<style src="@vueform/slider/themes/default.css">
</style>