<template>
  <router-link :to="`/motel-node-ui/rooms/${room.slug}`" tag="a" class="d-flex flex-row align-items-center">
    <div ref="carousel" class="carousel slide" data-bs-ride="carousel">
      <div class="carousel-inner">
        <div class="carousel-item" :class="{ 'active': index === activeIndex }" v-for="(image, index) in room.images"
          :key="index">
          <img :src="image.url" class="d-block image-item img-thumbnail" :alt="image.url">
        </div>
      </div>
    </div>
    <div class="room-info">
      <h3 class="fw-bolder">{{ room.name }}</h3>
      <div class="fs-5 text-dark fw-bolder"><span>Diện tích: {{ room.area }} m2</span></div>
      <div><span class="fs-5 text-danger fw-bolder">Giá: {{ toVnd }}</span></div>
      <div class="fs-5 text-dark fw-bolder"><span>Địa chỉ: {{ address }}</span></div>
      <div class="fs-5" :style="{ color: '#b1bcc8' }">
        <p class="room-description">{{ room.description }}</p>
      </div>
      <div class="fs-5 text-dark fw-bolder"><span>Liên hệ: {{ landlordContact }}</span></div>
    </div>
  </router-link>
</template>

<script>
import RoomFormatter from '../../utils/util'

export default {
  name: 'Room',
  props: {
    room: Object
  },
  data() {
    return {
      length: this.room.images.length,
      activeIndex: 0
    }
  },
  mounted() {
    setInterval(() => {
      this.activeIndex = this.activeIndex + 1 === this.length ? 0 : this.activeIndex + 1;
    }, 2000)
  },
  computed: {
    address() {
      return RoomFormatter.addressRoom(this.room)
    },
    toVnd() {
      return RoomFormatter.toVnd(this.room.price)
    },
    landlordContact() {
      return RoomFormatter.landlordContact(this.room)
    }
  },
  methods: {

  }
}
</script>

<style scoped>
a {
  background: #fff;
  transition: all 0.3s ease;
  text-decoration: none;
  padding: 8px;
  margin: 8px 0px;
  box-shadow: rgb(0 0 0 / 2%) 0px 1px 3px 0px, rgb(27 31 35 / 15%) 0px 0px 0px 1px;
}

a:hover {
  box-shadow: rgb(45 110 214 / 25%) 0px 54px 55px, 
              rgb(0 135 255 / 12%) 0px -12px 30px, 
              rgb(0 124 255 / 12%) 0px 4px 6px, 
              rgb(0 61 255 / 17%) 0px 12px 13px, 
              rgb(0 20 255 / 9%) 0px -3px 5px;
}

.image-item {
  min-width: 150px;
  max-width: 150px;
  height: 120px;
}

.carousel-item {
  transition: transform 2s ease, opacity .5s ease-out
}

.room-info {
  margin-left: 10px;
  text-align: left;
}

.room-description {
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
  overflow: hidden;
  display: -webkit-box;
}
</style>