class RoomFormater {
  static toVnd(value) {
    if (value == null || value == undefined) value = 0
    return new Intl.NumberFormat('vi-VN', { style: 'currency', currency: 'VND' }).format(value)
  }
  
  static time(timestamp) {
    return new Date(timestamp).toLocaleDateString()
  }
  
  static lines(description) {
    if (description == undefined) return ''
    return description.trim().split(/\r?\n/)
  }
  
  static addressRoom(room) {
    return [room.motel.street, room.motel.ward, room.motel.district, room.motel.province].join(', ')
  }
  
  static landlordContact(room) {
    return `${room.motel.landlord.fullname} - ${room.motel.landlord.phonenumber}`
  }
}

export default RoomFormater
