<template>
  <header>
    <nav class="navbar navbar-info">
      <router-link to="/motel-node-ui">
        <img class="logo-icon" src="@/assets/logo.png" alt="Logo Icon" />
      </router-link>
      <div v-if="user === undefined">
        <router-link to="/motel-node-ui/login">Đăng Nhập</router-link>
        <router-link to="/motel-node-ui/register">Đăng Ký</router-link>
      </div>
      <div class="d-flex fs-4 text-white fw-bold" v-else>
        <div class="user-fullname">{{ user.fullname }}</div>
        <div role="button" @click="logout()">Đăng xuất</div>
      </div>
    </nav>
  </header>
</template>

<script>
import { mapMutations, mapState } from 'vuex'
export default {
  created() {
    this.$store.commit('setUser')
    if (this.$store.state.user !== undefined) {
      this.$store.dispatch('refreshToken')
    }
  },
  computed: {
    ...mapState(['user'])
  },
  methods: {
    ...mapMutations(['setUser']),
    async logout() {
      try {
        await this.$store.dispatch('logout')
        window.location.href = '/motel-node-ui'
      } catch (error) {
        console.log(error)
      }
    }
  }
}
</script>

<style scoped>
.navbar {
  padding: 8px 50px;
  background: #2678e9;
}

.logo-icon {
  width: 75px;
  border-radius: 5px;
}

div>a {
  margin: 0px 10px;
  background: #fff;
  padding: 10px 15px;
  border-radius: 5px;
  color: #000;
  text-decoration: none;
  font-weight: 600;
  font-size: 18px;
  transition: all 0.3s ease;
}

div>a:hover {
  background: #ddd;
}

.user-fullname {
  margin-right: 20px;
}
</style>
