import axios from 'axios'
import jwtDecode from 'jwt-decode'
import { createStore } from 'vuex'

const store = createStore({
  state() {
    return {
      user: undefined,
      timer: undefined,
      loading: true
    }
  },

  getters: {

  },

  mutations: {
    setUser(state) {
      try {
        const accessToken = localStorage.getItem('accessToken')
        if (!state.user && accessToken) {
          const userDecoded = jwtDecode(accessToken)
          state.user = { ...userDecoded }
        }
      } catch (error) {
        console.log(error)
      }
    },

    setLoading(state, status) {
      state.loading = status
    }
  },

  actions: {
    refreshToken({ commit, state }) {
      state.timer = setInterval(async () => {
        try {
          const res = await axios.post('/auth/refresh-token', JSON.stringify({
            refreshToken: localStorage.getItem('refreshToken')
          }))
          localStorage.setItem('accessToken', res.data.accessToken)
          localStorage.setItem('refreshToken', res.data.refreshToken)
        } catch (error) {
          console.log(error)
        }
      }, 10 * 60 * 1000)
    },

    login({commit, state}, payload) {
      const user = {
        username: payload.username,
        password: payload.password
      }
      return new Promise(async (resolve, reject) => {
        try {
          const res = await axios.post('/auth/login', JSON.stringify(user), {
            headers: {
              'Content-Type': 'application/json'
            }
          })
          const userDecoded = jwtDecode(res.data.accessToken)
          state.user = { ...userDecoded }
          localStorage.setItem('accessToken', res.data.accessToken)
          localStorage.setItem('refreshToken', res.data.refreshToken)
          resolve(res)
        } catch (error) {
          reject(error)
        }
      })
    },

    logout({commit, state}) {
      return new Promise(async (resolve, reject) => {
        try {
          const res = await axios.delete('/auth/logout', {
            headers: {
              'Authorization': `Bearer ${localStorage.getItem('accessToken')}`
            }
          })
          clearInterval(state.timer)
          localStorage.clear()
          state.user = undefined
          resolve(res)
        } catch (error) {
          reject(error)
        }
      })
    },
  }

})

export default store
