<template>
  <div class="form-container">
    <form>
      <div class="row mb-3">
        <label for="fullname" class="col-sm-2 col-form-label">Họ tên</label>
        <div class="col-sm-10">
          <input type="text" class="form-control" id="fullname" v-model="register.fullname">
          <div class="error" v-if="error.fullname !== undefined">{{ error.fullname }}</div>
        </div>
      </div>
      <div class="row mb-3">
        <label for="phonenumber" class="col-sm-2 col-form-label">Số điện thoại</label>
        <div class="col-sm-10">
          <input type="text" class="form-control" id="phonenumber" v-model="register.phonenumber">
          <div class="error" v-if="error.phonenumber !== undefined">{{ error.phonenumber }}</div>
        </div>
      </div>
      <div class="row mb-3">
        <label for="username" class="col-sm-2 col-form-label">Tên đăng nhập</label>
        <div class="col-sm-10">
          <input type="text" class="form-control" id="username" v-model="register.username">
          <div class="error" v-if="error.username !== undefined">{{ error.username }}</div>
        </div>
      </div>
      <div class="row mb-3">
        <label for="password" class="col-sm-2 col-form-label">Mật khẩu</label>
        <div class="col-sm-10">
          <input type="password" class="form-control" id="password" v-model="register.password">
          <div class="error" v-if="error.password !== undefined">{{ error.password }}</div>
        </div>
      </div>
      <fieldset class="row mb-3">
        <legend class="col-form-label col-sm-2 pt-0">Bạn muốn</legend>
        <div class="col-sm-10">
          <div class="form-check">
            <input class="form-check-input" type="radio" name="role" id="user-role" value="user"
              v-model="register.role">
            <label class="form-check-label" for="user-role">
              Tìm phòng
            </label>
          </div>
          <div class="form-check">
            <input class="form-check-input" type="radio" name="role" id="landlord-role" value="landlord"
              v-model="register.role">
            <label class="form-check-label" for="landlord-role">
              Cho thuê phòng
            </label>
          </div>
        </div>
      </fieldset>
      <button type="button" class="btn btn-primary" @click="signup()">Đăng ký</button>
    </form>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name: 'Register',
  data() {
    return {
      register: {
        fullname: '',
        phonenumber: '',
        username: '',
        password: '',
        role: 'user'
      },
      error: {
        fullname: undefined,
        phonenumber: undefined,
        username: undefined,
        password: undefined
      }
    }
  },
  watch: {
    'register.fullname': function () {
      this.error.fullname = undefined
    },
    'register.phonenumber': function () {
      this.error.phonenumber = undefined
    },
    'register.username': function () {
      this.error.username = undefined
    },
    'register.password': function () {
      this.error.password = undefined
    },
  },
  methods: {
    async signup() {
      try {
        const res = await axios.post('/auth/register', JSON.stringify(this.register), {
          headers: {
            'Content-Type': 'application/json'
          }
        })
        window.alert('Register Successfully')
        window.location.href = '/login'
      } catch (error) {
        const errorMessages = error.response.data
        if (error.response.status === 422) {
          for (const property in errorMessages) {
            this.error[property] = errorMessages[property].msg
          }
        }
        if (error.response.status === 409) {
          this.error.username = errorMessages.message
        }
      }
    }
  }
}
</script>

<style scoped>
.form-container {
  height: 100vh;
}

form {
  width: 70%;
  margin: 0 auto;
  background: #fff;
  border-radius: 5px;
  padding: 20px;
}

.form-check {
  text-align: start;
}

.error {
  text-align: start;
  color: red;
  font-weight: 600;
}
</style>