<template>
  <div class="room-view container">
    <div class="room">
      <div id="carousel-images" class="carousel slide w-75" data-bs-ride="carousel">
        <div class="carousel-inner">
          <div class="carousel-item" :class="{ 'active': index === imgIdx }" v-for="(image, index) in room.images"
            :key="index">
            <img :src="image.url" class="d-block img-thumbnail" :alt="image.url">
          </div>
        </div>
        <button class="carousel-control-prev" type="button" data-bs-target="#carousel-images" data-bs-slide="prev"
          @click="moveSlider(-1)">
          <span class="carousel-control-prev-icon" aria-hidden="true"></span>
          <span class="visually-hidden">Previous</span>
        </button>
        <button class="carousel-control-next" type="button" data-bs-target="#carousel-images" data-bs-slide="next"
          @click="moveSlider(1)">
          <span class="carousel-control-next-icon" aria-hidden="true"></span>
          <span class="visually-hidden">Next</span>
        </button>
      </div>
      <div class="room-info">
        <h2 class="fw-bold text-danger">{{ room.name }}</h2>
        <div class="fs-4"><i class="fa-solid fa-location-dot"></i>Địa chỉ: {{ address }}</div>
        <div class="room-attributes d-flex align-items-center">
          <div class="attribute-item fs-4 fw-bold text-success">
            <i class="fa-solid fa-sack-dollar"></i>
            <span>Giá: {{ toVnd(room.price) }}</span>
          </div>
          <div class="attribute-item fs-4 fw-bold text-success">
            <i class="fa-solid fa-money-from-bracket"></i>
            <span>Tiền cọc: {{ toVnd(room.deposit) }}</span>
          </div>
          <div class="attribute-item fs-5 fw-bold">
            <i class="fa-solid fa-ruler"></i>
            <span>Diện tích: {{ room.area }} m<sup>2</sup></span>
          </div>
          <div class="attribute-item fs-5 fw-bold">
            <i class="fa-solid fa-clock"></i>
            <span>Ngày đăng: {{ time }}</span>
          </div>
        </div>
        <div>
          <h3 class="fw-bold text-dark">Thông tin mô tả</h3>
          <div>
            <p class="fs-5" v-for="(line, index) in lines(room.description)" :key="index">{{ line }}</p>
          </div>
        </div>
        <div>
          <h3 class="fw-bold text-dark">Dịch vụ</h3>
          <div>
            <p class="fs-5" v-for="(roomService, index) in room.services" :key="index">{{ roomService.service.name }}:
              {{
                  toVnd(roomService.price)
              }} / {{ roomService.unity }}</p>
          </div>
        </div>
        <div>
          <h3 class="fw-bold text-dark">Thông tin liên hệ</h3>
          <div class="fs-5">{{ contact }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import RoomFormatter from '../utils/util'
import axios from 'axios'
import { mapMutations } from 'vuex'

export default {
  name: 'RoomView',
  async created() {
    this.$store.commit('setLoading', true)
    const res = await axios.get(`/rooms/${this.$route.params.slug}`)
    this.room = res.data
    this.length = this.room.images.length
    this.$store.commit('setLoading', false)
  },
  data() {
    return {
      imgIdx: 0,
      length: 0,
      room: {}
    }
  },
  computed: {
    address() {
      if (Object.entries(this.room).length !== 0)
        return RoomFormatter.addressRoom(this.room)
      return ''
    },
    contact() {
      if (Object.entries(this.room).length !== 0)
        return RoomFormatter.landlordContact(this.room)
      return ''
    },
    time() {
      if (Object.entries(this.room).length !== 0)
        return RoomFormatter.time(this.room.createdAt)
      return ''
    }
  },
  methods: {
    ...mapMutations(['setLoading']),
    toVnd(value) {
      return RoomFormatter.toVnd(value)
    },
    time(timestamp) {
      return RoomFormatter.time(timestamp)
    },
    lines(description) {
      return RoomFormatter.lines(description)
    },
    moveSlider(step) {
      if (this.imgIdx + step === this.length) {
        this.imgIdx = 0
      }
      else if (this.imgIdx + step === -1) {
        this.imgIdx = this.length - 1
      }
      else {
        this.imgIdx += step
      }
    }
  }
}
</script>

<style scoped>
i {
  margin-right: 10px;
}

.room {
  padding: 20px;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
  background: #fff;
}

.room-info {
  margin-top: 20px;
  text-align: start;
}

.attribute-item {
  margin-right: 20px;
}

#carousel-images {
  margin: 0 auto;
}

.carousel-control-prev,
.carousel-control-next {
  transition: all 0.3s ease;
  background: #000;
  opacity: 0.25;
}

.carousel-control-prev:hover,
.carousel-control-next:hover {
  opacity: 0.5;
}

.carousel-item>img {
  margin: 0 auto;
}
</style>