<template>
  <div class="form-container">
      <form>
    <div class="row mb-3">
      <label for="username" class="col-sm-2 col-form-label">Tên đăng nhập</label>
      <div class="col-sm-10">
        <input type="text" class="form-control" id="username" v-model="user.username">
      </div>
    </div>
    <div class="row mb-3">
      <label for="password" class="col-sm-2 col-form-label">Mật khẩu</label>
      <div class="col-sm-10">
        <input type="password" class="form-control" id="password" v-model="user.password">
      </div>
    </div>
    <div class="row mb-3 error text-danger fw-bold justify-content-center" v-if="error !== ''">{{ error }}</div>
    <button type="button" class="btn btn-primary" @click="signin()">Đăng nhập</button>
  </form>
  </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  name: 'Login',
  data() {
    return {
      user: {
        username: '',
        password: ''
      },
      error: ''
    }
  },
  methods: {
    ...mapActions(['login']),
    async signin() {
      this.$store
        .dispatch('login', { ...this.user })
        .then(res => {
          window.location.href = '/motel-node-ui'
        })
        .catch(error => this.error = error.response.data.message)
    }
  }
}
</script>

<style scoped>
.form-container {
  height: 100vh;
}

form {
  width: 70%;
  margin: 0 auto;
  background: #fff;
  border-radius: 5px;
  padding: 50px;
}
</style>