<template>
  <transition name="fade">
    <div class="vue-modal" :class="[className]" v-show="open">
      <transition name="drop-in">
        <div class="vue-modal-inner" v-show="open">
          <div class="vue-modal-header d-flex align-items-center">
            <i class="fa-solid fa-arrow-left" @click="$emit(emitFuncName)"></i>
            <span class="vue-modal-header-label">{{ headerLabel }}</span>
          </div>
          <div class="vue-modal-content d-flex flex-column align-items-center">
            <slot></slot>
          </div>
        </div>
      </transition>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'Modal',
  props: {
    open: {
      type: Boolean,
      default: true
    },
    headerLabel: String,
    className: String,
    emitFuncName: String
  },
}
</script>

<style scoped>
*,
::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.vue-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow-x: hidden;
  overflow-y: auto;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 1;
}

.vue-modal-inner {
  box-shadow: rgba(0, 0, 0, 0.25) 0px 54px 55px, 
              rgba(0, 0, 0, 0.12) 0px -12px 30px, 
              rgba(0, 0, 0, 0.12) 0px 4px 6px, 
              rgba(0, 0, 0, 0.17) 0px 12px 13px, 
              rgba(0, 0, 0, 0.09) 0px -3px 5px;
  max-width: 500px;
  margin: 13rem auto;
  position: relative;
  background-color: #fff;
  border: 1px solid rgba(0, 0, 0, 0.3);
  background-clip: padding-box;
  border-radius: 0.3rem;
  padding: 1rem;
}

.vue-modal-header {
  font-size: x-large;
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
}

i {
  margin-right: 10px;
  cursor: pointer;
}

.modal--room-location .vue-modal-content {
  width: 100%;
